@import (reference) "../../common.less";
.expand-list {
  display: flex;
  position: relative;
  .expand-list-icon {
    color: @white;
    font-size: 24px;
    border: solid 1px @second-white;
    border-radius: 4px;
    cursor: pointer;
    transition: all .2s;
    &:hover {
      color: @yellow;
      border-color: @yellow;
    }
  }
  .expand-list-content {
    display: flex;
    position: absolute;
    height: 0;
    top: 16px;
    flex-direction: column;
    align-items: flex-end;
    right: 0;
    padding-top: 4px;
    opacity: 0;
    transition: all .2s;
    z-index: 9;
    transform: scaleY(0);
    .expand-list-arrow {
      display: inline-block;
      width: 0;
      height: 0;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 6px solid @component-background;
      margin-right: 8px;
    }
    .expand-list-container {
      display: flex;
      flex-direction: row;
      width: auto;
      border-radius: 2px;
      height: 40px;
      padding-left: 6px;
      margin-right: -18px;
      background: @component-background;
      li {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        .expand-list-icon-container {
          display: inline-block;
          margin-right: 6px;
          width: 24px;
          height: 24px;
          border-radius: 2px;
          border: solid 1px @second-white;
          svg {
            width: 24px;
            height: 24px;
            margin-right: 5px;
            stroke-width: 1px;
            stroke: @second-white;
          }
        }
      }
      li:hover {
        .expand-list-icon-container {
          border-color: @yellow;
          svg {
            stroke: @yellow;
          }
        }
      }
    }
  }
}

.expand-list:hover {
  .expand-list-content {
    top: 26px;
    height: auto;
    opacity: 1;
    transform: scaleY(1);
  }
}
