@import (reference) "../../common.less";
.input-group {
  display: flex;
  flex-direction: row;
  align-items: center;
  input {
    background: @component-background;
    height: 22px;
    border-radius: 4px;
    border: solid @white 1px;
    width: 22px;
    margin-left: 10px;
    color: @white;
    outline: none;
    padding-left: 4px;
    padding-right: 4px;
    font-size: 12px;
    &:hover {
      border-color: @yellow;
    }
    &:focus {
      border-color: @yellow;
      box-shadow: 0 0 0 2px rgba(255, 152, 0, .2);
    }
  }
}
