@import (reference) "../../common.less";
@setting-width: 400px;
@header-height: 60px;
.kline {
  height: 100%;
  display: flex;
  flex-direction: column;
  .kline-header {
    height: 60px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    color: @white;
    font-size: 18px;
    line-height: @header-height;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
    z-index: 10;
    box-shadow: 0 2px 8px #151515;
    .kline-header-version {
      font-size: 16px;
    }
    .kline-header-link-icon {
      font-size: 26px;
      color: @white;
      &:hover {
        color: @second-white;
        border-color: @second-white
      }
    }
  }

  .kline-content-container {
    display: flex;
    flex-direction: row;
    flex: 1;
    background: #2E3038;
    .kline-setting-container {
      height: 100%;
      width: @setting-width!important;
      border-right: solid @component-background 1px;
      .kline-setting-list-title {
        height: 36px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        font-size: 16px;
        color: @white;
        padding-right: 16px;
        cursor: pointer;
        transition: all .2s;
        .kline-setting-list-title-text {
          display: inline-block;
          padding-left: 16px;
          border-left: solid 4px transparent;
        }
        .kline-setting-list-title-icon {
          font-size: 22px;
          transition: all .3s;
        }
        &:hover {
          background-color: #2A2A2A;
          color: @second-white;
          .kline-setting-list-title-text {
            border-left-color: @yellow;
          }
        }
      }
      .kline-setting-list-title-expand {
        .kline-setting-list-title-text {
          border-left-color: @yellow;
        }
        .kline-setting-list-title-icon {
          transform: rotate(180deg);
        }
      }
      .kline-setting-list {
        padding-left: 20px;
        padding-right: 20px;
        font-size: 14px;
        display: none;
        .kline-setting-list-row {
          display: flex;
          flex-direction: row;
          align-items: center;
          justify-content: space-between;
          height: 40px;
          border-bottom: solid 1px #404040;
          transition: all .5s;
          color: @gary;
          &[datatype="sub"] {
            justify-content: flex-start;
            border-bottom-color: transparent;
            color: @second-white!important;
            &:hover {
              color: @second-white!important;
            }
            .kline-setting-list-row-arrow {
              display: inline-block;
              margin-right: 8px;
              font-style: normal;
              width: 8px;
              height: 8px;
              border-radius: 50%;
              background: @yellow;
            }
          }
          &:hover {
            color: @yellow;
          }
        }
      }

      .kline-setting-list-expand {
        display: block;
        margin-bottom: 10px;
      }
    }
    .kline-chart-container {
      display: flex;
      flex: 1;
      position: relative;
      height: 100%;
      .kline-chart {
        height: 100%;
        width: 100%;
      }
      .kline-chart-loading-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        background: rgba(0, 0, 0, .3);

        .kline-chart-loading {
          position: relative;
          width: 26px;
          height: 26px;
          pointer-events: none;
          background: @yellow;
          animation: kline-chart-loading 1.2s infinite ease-in-out;
        }
        @keyframes kline-chart-loading {
          0% {
            transform: perspective(120px) rotateX(0deg) rotateY(0deg);
          }
          50% {
            transform: perspective(120px) rotateX(-180.1deg) rotateY(0deg);
          }
          100% {
            transform: perspective(120px) rotateX(-180deg) rotateY(-179.9deg);
          }
        }

        .kline-chart-loading-text {
          display: inline-block;
          margin-top: 6px;
          color: @second-white;
          font-size: 12px;
        }
      }
    }
  }
}

@media only screen and (max-width: 768px) {
  .kline {
    .kline-content-container {
      .kline-setting-container {
        display: none;
      }
      .kline-chart-container {
        width: 100%;
      }
    }
  }
}
